@media screen and (max-width: 500px){
	.header{
		nav{
			.logo{
				img{
					width: 100px;
				}
			}
		}
		.slider{
			.single-item{
				.carousel-1,.carousel-3{
					background-position: 50% 50%;
					height: 50vh;
				}
			}
		}
	}
	#section{
		.agency{
			.blocks{
				display: block;
				.block{
					.size(12);
				}
			}
		}
		.akita{
			.items{
				.item{
					display: flex;
					flex-direction: column;
					.title{
						padding: 20px;
						p{
							span{
								font-size: 16px;
							}
						}
					}
					.image2{
						order: 2;
					}
					.image1,.image2{
						.imgnone{
							display: block;
						}
					}
				}
			}
		}
		.contacts{
			display: block;
			&__phone{
				margin-bottom: 30px;
			}
		}
	}
}